/* CARD ELEMENTS */
.card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 60px;
}

.card-title{
  min-height: 100%;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
.card-title:hover{
  cursor: pointer;
}

.cardimg-wrapper{
  width: 100%;
  overflow: hidden;
}

.card-image {
  width: 100%;
  height: auto;
  -webkit-animation: fadeIn 1.5s;
          animation: fadeIn 1.5s;
}

.card-text {
  padding: 20px;
  -webkit-animation: fadeIn 1.5s;
          animation: fadeIn 1.5s;
  margin: 0;
}

.card-price {
  font-weight: bold;
  padding: 0;
  -webkit-animation: fadeIn 1.5s;
          animation: fadeIn 1.5s;
  margin: 0;
  color: green;
}

.card-name {
  position: absolute;
  top: 0;
  left: 0;
  font-family: 'Josefin Sans', sans-serif;
  fill: white;
  font-size: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

/* SVG ANIMATED PART */
.circular:hover > .card-shape {
  -webkit-animation: 0.5s circular linear forwards;
  animation: 0.5s circular linear forwards;
  fill: #40C4FF;
  stroke: #FAFAFA;
  fill-opacity: 1;
}

/* CARD STATES */
.card-close {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 30px;
  font-weight: bold;
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  height: 40px;
  width: 40px;
  line-height: 40px;
  text-align: center;
  color: white;
}
.card-close:hover {
  cursor: pointer;
}

.card.closed{
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  padding: 10px 0px 10px 0px;
  background-size: cover;
  background-position-y: 50%;
}

.card.opened{
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: white !important;
  height: calc(100% - 50px);;
  position: absolute;
  z-index: 2;
  -webkit-transition: height 0.6s ease;
  -o-transition: height 0.6s ease;
  transition: height 0.6s ease;
}

.card.opened .card-title{
  min-height: initial;
  position: absolute;
  top: 0;
  pointer-events: none;
}

.card.opened .card-title.circular{
  width: 320px;
  z-index: 1;
}

.card.opened .card-title.circular .card-shape{
  width: 320px;
  x: -70px;
  fill-opacity: 1;
}

.card.opened .card-title.circular:hover .card-shape{
  stroke: none;
}


/* CARD BUTTONS FROM OPENED STATE*/
.btn-accept {
  padding: 10px 30px;
  background-color: green;
  color: white;
  border: none;
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
  border-radius: 20px;
  outline: 0;
}
.btn-accept:hover{
  cursor: pointer;
  background-color: lightgreen;
  color: green;
  font-weight: bold;
  border: 1px solid green;
}

.choice-controls{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 220px;
  height: 100px;
}

.card-shape {
  -webkit-transition: all 1.0s ease;
  -o-transition: all 1.0s ease;
  transition: all 1.0s ease;
  fill-opacity: 0.8;
}

/* ANIMATIONS */
@-webkit-keyframes circular {
  from {
    stroke-dasharray: 0 940;
    stroke-dashoffset: 0;
    stroke-width: 10px;
  }
  to {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
    stroke-width: 8px;
  }
}
@keyframes circular {
  from {
    stroke-dasharray: 0 940;
    stroke-dashoffset: 0;
    stroke-width: 10px;
  }
  to {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
    stroke-width: 8px;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



